export const newsFetching=()=>{
  return{
      type:"NEWS_FETCHING"
  }
}

export const newsFetched=(news)=>{
  return{
    type:"NEWS_FETCHED",
    peyload:news
  }
}
export const newsFetchingError=()=>{
  return{
    type:"NEWS_FETCHING_ERROR"
  }
}

export const newsCreate=(news)=>{
  return{
    type:"NEWS_CREATED",
    peyload:news
  }
}
export const newsDelete=(id)=>{
  return{
    type:"NEWS_DELETE",
    peyload:id
  }
}