
import React from "react";
import { useState } from "react";
import { v4 } from "uuid";
import { useHttp } from "../hook/useHttp";
import { useDispatch } from "react-redux";
import { newsCreate } from "../ReactRedux/action";

function NewsAddForm(props) {
  const [name, setName] = useState("");
  const [productTypeId , setProductTypeId]=useState("")
  const [discription, setDiscription] = useState("");
  const [cost , setCost]=useState("");
  const [date ,setDate]=useState("")
  const dispatch = useDispatch();
  const { request } = useHttp();

  const onSubmitHandle = (e) => {
    e.preventDefault();
    const newNews = { id: v4 ,product_type_id:productTypeId,name_uz:name,
       cost,address:discription, created_date:date };
    request(
      "http://94.158.54.194:9092/api/product",
      "POST",
      JSON.stringify(newNews),
    )
      .then((res) => console.log("success"))
      .then(dispatch(newsCreate(newNews)))
      .catch((err) => console.log(err));

    setName("");
    setProductTypeId();
    setCost("");
    setDate("");
    setDiscription("");
    console.log(newNews);
  };

  return (
    <form className="border p-4 shadow-lg rounded" onSubmit={onSubmitHandle}>
      <div className="mb-3">
        <label htmlFor="name" className="form-label fs-4">
          Name for new news{" "}
        </label>
        <input
          type="text"
          required
          name="name"
          className="form-control"
          id="name"
          placeholder="what is name of news?"
          value={name}
          onChange={(e) => setName(e.target.value)}
        />
      </div>
      <div className="mb-3">
        <label htmlFor="number" className="form-label fs-4">
        product_type_id {" "}
        </label>
        <input
          type="number"
          required
          name="product_type_id"
          className="form-control"
          id="product_type_id"
          placeholder="product_type_id"
          value={productTypeId}
          onChange={(e) => setProductTypeId(e.target.value)}
        />
      </div>
      <div className="mb-3">
        <label htmlFor="name" className="form-label fs-4">
          Cost {" "}
        </label>
        <input
          type="text"
          required
          name="cost"
          className="form-control"
          id="cost"
          placeholder="new Price?"
          value={cost}
          onChange={(e) => setCost(e.target.value)}
        />
      </div>
      <div className="mb-3">
        <label htmlFor="date">
          Date {""}
        </label>
        <input type="date" name="date" id="date"
        placeholder="new Date"
        className="form-control"
        value={date}
        onChange={(e)=>setDate(e.target.value)}
        />
      </div>
      <div className="mb-3">
        <label htmlFor="text" className="form-label fs-4">
          Address Discription {" "}
        </label>
        <textarea
          type="text"
          required
          name="text"
          className="form-control"
          id="text"
          placeholder="what is your news about?"
          style={{ height: "120px" }}
          value={discription}
          onChange={(e) => setDiscription(e.target.value)}
        />
      </div>
      
      <button type="submit" className="btn btn-dark text-white w-100 shadow-lg">
        Create News
      </button>
    </form>
  );
}

export default NewsAddForm;

