import React from 'react';
import NavBar from './NavBar';
import NewsList from './NewsList';
import NewsAddForm from './NewsAddForm';

function App() {
  return (
    <div className='app'>
      <NavBar/>
      <div className='content'>
        <NewsList/>
        <div className='content__page'>
          <NewsAddForm/>
        </div>
      </div>
    </div>
  );
}

export default App;