
import { useHttp } from "../hook/useHttp"
import { useEffect , useCallback } from "react"
import { useSelector , useDispatch } from "react-redux";
import { newsFetched , newsFetching ,newsFetchingError ,newsDelete } from "../ReactRedux/action";
import Spinner from "./Spinner";
import Error from "./Error"
import NewsListItem from "./NewsListItem"
export default function NewsList(){
  const {request}=useHttp();
  const {news , newsLoadingStatus}=useSelector((state)=>state)
  const dispatch=useDispatch();
  console.log(newsLoadingStatus);
  console.log(news);
  

  useEffect(()=>{
    dispatch(newsFetching())
    request("http://94.158.54.194:9092/api/product")
    .then(data=>dispatch(newsFetched(data)))
    .catch(()=>dispatch(newsFetchingError()))

    // eslint-disable-next-line
  },[])

  const onDelete=useCallback((id)=>{
    request(`http://94.158.54.194:9092/api/product/${id}` , "DELETE" ,)
    .then(data=>console.log("Delete" + data))
    .then(dispatch(newsDelete(id)))
    .catch(err=>console.log(err))
    //eslint-disable-next-line
  } ,[])

  if(newsLoadingStatus ==="loading"){
    return <Spinner/>
  }else if(newsLoadingStatus==="error"){
    return <Error/>
  }
  
  const renderNewsList=(arr)=>{
    if(arr.length ===0){
      <h4 className="text-center mt-5">News doesn't exists</h4>
    }
    return arr.map(({id , ...props})=>{
      return <NewsListItem key={id} onDelete={()=>onDelete(id)} {...props} />
    })
  }
  const element=renderNewsList(news)

  return(
    <ul>
      {element}
    </ul>
  )
}