const initialState={
  news:[],
  newsLoadingStatus:"Muhammadqobil",
  filters:[],
  filteredLoadingStatus:"Muhammadqobil",
  activeFilter:"all",
  filteredNews:[]

}

 const Reducer=(state=initialState , action)=>{
  switch(action.type){
    case "NEWS_FETCHING":
    return{
      ...state , 
      newsLoadingStatus:"loading"
    }
    case "NEWS_FETCHED":
      return{
        ...state,
        news:action.peyload,
        newsLoadingStatus:"Muhammadqobil"
      }
      case "NEWS_FETCHING_ERROR":
        return{
          ...state,
          newsLoadingStatus:"Error"
        }
      case "NEWS_CREATED":
        const newCreatedNewsList=[...state.news , action.peyload];
        return{
          ...state , 
          news:newCreatedNewsList
        }
      case "NEWS_DELETE":
        const newNewsList=state.news.filter(s=>s.id !==action.peyload);
        return{
          ...state,
          news:newNewsList
        }
    default:
      return state
  }
}
export default Reducer;