import React from 'react';
import news from "../assets/news.jpg"

function NewsListItem(props) {
  const {name_uz , address , created_date ,cost ,product_type_id, onDelete }=props
  let elementClassName;
  switch(product_type_id){
    case 0:
      elementClassName = "bg-danger bg-gradient"
      break;
    case 1:
      elementClassName="bg-primary bg-gradient"
      break;
    case 2:
      elementClassName="bg-success bg-gradient"
      break;
    case 3:
      elementClassName="bg-secondary bg-gradient"
      break;
      default:
        elementClassName="bg-dark bg-gradient"
  }
  return (
    <li className={`card flex-row shadow-lg text-white ${elementClassName}`}>
      <div className='card-body'>
        <h3 className='card-title'>{name_uz}</h3>
        <p className='card-text'>{address} </p>
        <p className='card-text'>{cost} </p>
        <p className='card-text'>{created_date}</p>
      </div>
      <img src={news} alt="news" className='img-fluid w-25 d-inline'
      style={{"objectFit":'cover'}}
      />
      <span className='position-absolute top-0 start-90 translate-middle badge border rounded-pill bg-light'>
        <button onClick={onDelete} type='button'  className='btn-close' aria-label='Close'></button>
      </span>
    </li>
  );
}

export default NewsListItem;